<template>
    <nav class="navbar fixed-top navbar-expand-lg navbar-dark bg-dark fixed-top">
      <div class="container">
        <a class="navbar-brand" href="index.html">My Music Mastery</a>
        <button class="navbar-toggler navbar-toggler-right collapsed" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="navbar-collapse collapse" id="navbarResponsive" style="">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item">
              <span class="nav-link" @click="goTo('sheets')">Practice Sheets</span>
            </li>
            <li class="nav-item dropdown">
              <a class="nav-link dropdown-toggle" href="#" id="navbarDropdownPortfolio" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                Customize
              </a>
              <div class="dropdown-menu dropdown-menu-right" aria-labelledby="navbarDropdownPortfolio">
                <span class="dropdown-item" @click="goTo('customPieces')">Music Pieces</span>
                <span class="dropdown-item" @click="goTo('customStrategies')">Practice Strategies</span>
              </div>
            </li>
            <li class="nav-item">
              <span class="nav-link" @click="goTo('profile')">{{userName}} Profile</span>
            </li>
          </ul>
        </div>
      </div>
    </nav>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: 'Header',
  props: {
  },
  beforeMount: function(){
    this.$store.dispatch('instructor/checkIfInstructor');
  },
  mounted: function(){

  },
  computed: mapState({
    login: state => state.container.login,
    newEntry: state => state.container.newEntry,
    profile: state => state.container.profile,
    userName: state => state.user.user.name,
    home: state => state.container.home,
    admin: state => state.container.admin,
    entryUID: state => state.container.entryUID,
    sheet: state => state.container.sheet,
    instructor: state => state.container.sheet,
    isInstructor: state => state.instructor.isInstructor,
    containerView: state => state.container.containerView
  }),
  methods:{
    updateNav(newEntry,profile,home,admin,instructor){
      this.$store.commit('container/setNewEntry',newEntry);
      this.$store.commit('container/setProfile',profile);
      this.$store.commit('container/setHome',home);
      this.$store.commit('container/setAdmin',admin);
      this.$store.commit('container/setSheet',false);
      this.$store.commit('container/setInstructor',instructor)
    },
    goTo(ind){
      window.history.pushState(null,ind,'/'+ind);
      $('#navbarResponsive').removeClass('show');
      if(ind === 'sheets' || ind.substring(0,6) === 'custom'){
        this.$store.commit('container/setSelection',ind);
        this.$store.commit('container/setContainerView','home');
      }
      else {
        this.$store.commit('container/setContainerView',ind);
      }
    }
  }
}
</script>

