const state = {
    login:false,
    newEntry:false,
    profile:false,
    home:true,
    admin:false,
    sheet:false,
    instructor:false,
    entryUID:'',
    existingEntry:'',
    selection:'entries',
    selectionMap: {
      'sheets':true,
      'entries':true,
      'customStrategies':true,
      'customPieces':true
    },
    containerView:'home'
}

  // getters
const getters = {}

// actions
const actions = {}

// mutations
const mutations = {
    exitEntry(state){
      state.newEntry = false;
      state.entryUID = '';
      state.existingEntry = '';
      state.home = true;
      state.containerView = 'home';
    },
    setSheet (state, val){
      state.sheet = val;
    },
    setLogin (state, val) {
      state.login = val;
    },
    setNewEntry (state, val) {
      state.newEntry = val;
    },
    setProfile (state, val) {
      state.profile = val;
    },
    setHome (state, val) {
      state.home = val;
    },
    setAdmin(state, val) {
      state.admin= val;
    },
    setInstructor(state, val){
      state.instructor = val;
    },
    setEntryUID (state, val) {
      state.entryUID = val;
    },
    setExistingEntry (state, val) {
      state.existingEntry = val;
    },
    setSelection (state,val){
      state.selection = val;
    },
    setContainerView (state,val){
      state.containerView = val;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }